// IMPORTS
// Material UI
import AppBar from '@material-ui/core/AppBar'
import Dialog from '@material-ui/core/Dialog'
import IconButton from '@material-ui/core/IconButton'
import Slide from '@material-ui/core/Slide'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import CloseIcon from '@material-ui/icons/Close'
import PropTypes from 'prop-types'

// Externals imports (Dependencies)
import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

// Internal imports
import EnhancedComponent from '../../components/EnhancedComponent'
import styles from '../../../resources/theme/distributionCenters'
import { edit, fetchStores, update } from '../StoreActions'
import { getEditingStore } from '../StoreReducer'
import Form from './Form'

function Transition(props) {
  return <Slide direction="down" {...props} />
}

class EditStore extends EnhancedComponent {
  state = {
    fields: {
      description: '',
      CNPJ: '',
      min_freight: '',
      CEP: '',
      street: '',
      number: '',
      complement: '',
      bairro: '',
      city: '',
      state: '',
    },
  }

  componentDidMount() {
    this.props.edit(this.props.match.params.id)
  }

  onSubmit = values => {
    const { fetchStores } = this.props
    // valida os campos se necessário
    this.props.update(values).then(data => {
      fetchStores()
      if (data && data.code === 200) {
        this.props.history.push('/stores')
      }
    })
  }

  onClose = () => {
    this.props.history.push('/stores')
  }

  render() {
    const { classes, store } = this.props
    return (
      <Dialog
        fullScreen
        open={store !== null}
        onClose={this.onClose}
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <Typography variant="h6" color="inherit" className={classes.flex}>
              Editar Lojista
            </Typography>
            <IconButton
              color="inherit"
              onClick={this.onClose}
              aria-label="Close"
            >
              <CloseIcon />
            </IconButton>
          </Toolbar>
        </AppBar>

        <Form data={store} onClose={this.onClose} onSubmit={this.onSubmit} />
      </Dialog>
    )
  }
}

EditStore.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  roles: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  roles: state.store.roles || [],
  store: getEditingStore(state),
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      update,
      edit,
      fetchStores,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(EditStore))
